import React, { useEffect, useState } from "react";
import styled from "styled-components";
import Header from "../../components/Header";
import icon_help from "../../img/icon_help.svg";
import { client } from "../../api";
import { useResetRecoilState } from "recoil";
import { stepState } from "../../atoms/step";
import { Link } from "react-router-dom";
import { Spinner } from "@radix-ui/themes";

const Conclusion1 = () => {
  const [conclusion, setConclusion] = useState("");
  const onResetStep = useResetRecoilState(stepState);
  const [timer, setTimer] = useState(true);

  //    
  const getConclusion = async () => {
    try {
      const response = await client.get("/judge/conclusion");
      console.log(response);
      setConclusion(response.data.feedback);
      setTimer(false);
    } catch (err) {
      console.log(err);
    }
  };

  //      
  const toMainPage = async () => {
    try {
      const response = await client.delete("/session");
      console.log(response);
    } catch (err) {
      console.log(err);
    }
    onResetStep();
  };

  //     getConclusion 
  useEffect(() => {
    getConclusion();
  }, []);

  return (
    <>
      <Header />
      <ScrollDomain>
        <Container>
          <TopWrapper>
            <Title>#1.   </Title>
            <HelpIcon src={icon_help} alt="icon-help" />{" "}
            <StudentInfo>
              <span style={{ fontSize: "14px" }}> 2 1</span>
              <span style={{ fontSize: "16px" }}> </span>
            </StudentInfo>
          </TopWrapper>
          <DevideLine></DevideLine>
          {/* spinner  UI  - timer state   */}
          {timer ? (
            <Spinner style={{ height: "72px" }} />
          ) : (
            <CCWrapper>{conclusion}</CCWrapper>
          )}
          <Link to="/">
            <ExitBtn onClick={toMainPage}></ExitBtn>
          </Link>
        </Container>
      </ScrollDomain>
    </>
  );
};

const ScrollDomain = styled.div`
  position: fixed;
  top: 64px;
  width: 100%;
  height: calc(100vh - 64px); // header  
  overflow-y: scroll;
`;
const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  text-align: center;
  padding: 32px;
`;

const TopWrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: end;
  position: relative;
`;
const Title = styled.div`
  padding-left: 24px;
  font-family: NanumSquareB;
  font-size: 28px;
  font-weight: 700;
`;

const HelpIcon = styled.img`
  margin-left: 24px;
  margin-bottom: 4px;
  height: 20px;
`;

const StudentInfo = styled.div`
  display: flex;
  flex-direction: column;
  align-items: end;
  position: absolute;
  right: 0;
  font-family: NanumSquareR;
  font-weight: 400;
`;

const DevideLine = styled.div`
  width: 100%;
  height: 1px;
  background-color: black;
  margin: 12px 0;
`;

const CCWrapper = styled.div`
  width: 100%;
  margin-top: 20px;
  padding: 0 64px;
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  align-items: cetner;
  font-family: NanumSquareR;
  font-weight: 400;
  font-size: 24px;
`;

const ExitBtn = styled.button`
  border: none;
  background-color: #f76f61;
  font-family: NanumSquareB;
  font-weight: 700;
  font-size: 20px;
  padding: 10px 24px;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 64px;
  width: 100%;
`;
export default Conclusion1;
