import React from "react";
import { useRecoilValue } from "recoil";
import { stepState } from "../../../atoms/step";
import styled from "styled-components";
import step5_1 from "../../../img/slide3_step5_1.svg";
import step5_2 from "../../../img/slide3_step5_2.svg";
import StepText6 from "./shortanswerQ/StepText1-6";
import StepText8 from "./shortanswerQ/StepText1-8";

const StepText = () => {
  const step = useRecoilValue(stepState);

  return (
    <>
      {/** 1  */}
      {step === 1 && (
        <Text>
          []   ,
          <br />     
          <br />
           3  
          <br />
             
          <br />
           1 .
        </Text>
      )}
      {step === 2 && (
        <Text>
          []   ,
          <br />
            A, B   AB
          <br />
           
          <br />
            B, C   BC
          <br />
           .
        </Text>
      )}
      {step === 3 && (
        <Text>
          []   ,
          <br />
            
          <br />
             B_1
          <br />
          .
        </Text>
      )}
      {step === 4 && (
        <Text>
          []   ,
          <br />
            A, C   AC
          <br />
           .
          <br />
           AC 
          <br />
           B_1 ?
        </Text>
      )}
      {step === 5 && (
        <>
          <Text>
             
            <img
              src={step5_1}
              alt="step5_1"
              style={{ margin: "0 4px 0 8px", height: "24px" }}
            />
            
          </Text>
          <Text>
             
            <img
              src={step5_2}
              alt="step5_2"
              style={{ margin: "0 4px 0 8px", height: "24px" }}
            />{" "}
             
          </Text>

          <Text>
              .
            <br />
            []   ,
            <br />
              A B_1,   B B_1,
            <br />
              C B_1  
            <br />
               
            <br />
               .
          </Text>
        </>
      )}
      {step === 6 && <StepText6 />}
      {step === 7 && (
        <Text>
          [ :   ] 
          <br />
           ,   B_1 A 
          <br />
            B_1  
          <br />
           A   .
          <br />
               B, C
          <br />
          ?
        </Text>
      )}
      {step === 8 && <StepText8 />}
      {step === 9 && (
        <Text>
           
          <br />
             
          <br />  ?
          <br />
              ?
        </Text>
      )}
    </>
  );
};

const Text = styled.div`
  font-family: NanumSquareR;
  font-weight: 400;
  font-size: 18px;
  display: flex;
  flex-wrap: wrap;

  line-height: 1.6;
`;

export default StepText;
