import React from "react";
import { useRecoilValue } from "recoil";
import { stepState } from "../../../atoms/step";
import styled from "styled-components";
import { useParams } from "react-router-dom";

const Tip = () => {
  const { id } = useParams();
  const step = useRecoilValue(stepState);
  return (
    <>
      {id === "1" && step === 1 && (
        <TipWrapper>
          <span
            style={{
              width: "fit-content",
              backgroundColor: "#FFE8A0",
              padding: "2px 8px",
            }}
          >
             TIP
          </span>
          <TipContent>
              
            <br />
              
            <br />
             .
          </TipContent>
        </TipWrapper>
      )}
      {id === "1" && step === 2 && (
        <TipWrapper>
          <span
            style={{
              width: "fit-content",
              backgroundColor: "#FFE8A0",
              padding: "2px 8px",
            }}
          >
             TIP
          </span>
          <TipContent>
            [] 
            <br />
            []   .
          </TipContent>
        </TipWrapper>
      )}
      {id === "1" && step === 3 && (
        <TipWrapper>
          <span
            style={{
              width: "fit-content",
              backgroundColor: "#FFE8A0",
              padding: "2px 8px",
            }}
          >
             TIP
          </span>
          <TipContent>
              
            <br />
             ESC 
            <br />
            [] .
          </TipContent>
        </TipWrapper>
      )}
      {id === "1" && step === 5 && (
        <TipWrapper>
          <span
            style={{
              width: "fit-content",
              backgroundColor: "#FFE8A0",
              padding: "2px 8px",
            }}
          >
             TIP
          </span>
          <TipContent>
            [] 
            <br />
            []   .
          </TipContent>
        </TipWrapper>
      )}
    </>
  );
};

const TipWrapper = styled.div`
  width: 100%;
  margin-top: 64px;
  padding: 0 24px;
  font-family: NanumSquareR;
  font-weight: 400;
  color: #4d4d4d;
  font-size: 16px;
`;

const TipContent = styled.div`
  padding-left: 8px;
  margin-top: 16px;
  font-size: 15px;
  line-height: 1.4;
`;

export default Tip;
