import React, { useState } from "react";
import styled from "styled-components";
import { useRecoilState, useResetRecoilState, useRecoilValue } from "recoil";
import { stepState } from "../../../atoms/step";
import StepText from "./StepText";
import Tip from "./Tip";
import { Link, useParams } from "react-router-dom";
import { AlertDialog, Flex, Button, Spinner } from "@radix-ui/themes";
import { client } from "../../../api";
import { answers6 } from "../../../atoms/answers6";
import { answers8 } from "../../../atoms/answers8";

const QuestionBox = () => {
  const { id } = useParams();
  const [step, setStep] = useRecoilState(stepState);
  const onResetStep = useResetRecoilState(stepState);

  const [timer, setTimer] = useState(true);

  const [answerInput4, setAnswerInput4] = useState("");
  const [answerInput7, setAnswerInput7] = useState("");
  const [answerInput9, setAnswerInput9] = useState("");

  // 6    
  const answersSix = useRecoilValue(answers6);
  const onResetAnswer6 = useResetRecoilState(answers6);

  // 8    
  const answersEight = useRecoilValue(answers8);
  const onResetAnswer8 = useResetRecoilState(answers8);

  const [feedback1, setFeedback1] = useState("");
  const [feedback2, setFeedback2] = useState("");
  const [feedback3, setFeedback3] = useState("");
  const [feedback4, setFeedback4] = useState("");
  const [feedback5, setFeedback5] = useState("");
  const [feedback6, setFeedback6] = useState("");
  const [feedback7, setFeedback7] = useState("");
  const [feedback8, setFeedback8] = useState("");
  const [feedback9, setFeedback9] = useState("");

  // [ ]   
  const toPrevStep = () => {
    setStep(step - 1);
    setTimer(true);
  };

  // [ ]   
  const toNextStep = () => {
    setStep(step + 1);
    setTimer(true);
  };

  //      
  const toMainPage = async () => {
    try {
      const response = await client.delete("/session");
      console.log(response);
    } catch (err) {
      console.log(err);
    }
    onResetStep();
  };

  //     
  const requestSubmitAnswer = async () => {
    if (step === 1) {
      try {
        setTimer(true);
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 1,
          answer:
            "1.   . 2.  3 . 3.     1 .",
        });
        console.log(response);
        setTimer(false);
        setFeedback1(response.data.feedback);
      } catch (err) {
        console.log(err);
      }
    } else if (step === 2) {
      try {
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 2,
          answer:
            "1.   . 2.  AB  . 3. BC  .",
        });
        console.log(response);
        setTimer(false);
        setFeedback2(response.data.feedback);
      } catch (err) {
        console.log(err);
      }
    } else if (step === 3) {
      try {
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 3,
          answer:
            "1.  . 2.    B_1 .",
        });
        console.log(response);
        setTimer(false);
        setFeedback3(response.data.feedback);
      } catch (err) {
        console.log(err);
      }
    } else if (step === 4) {
      try {
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 4,
          answer: answerInput4,
        });
        console.log(response);
        setTimer(false);
        setFeedback4(response.data.feedback);
      } catch (err) {
        console.log(err);
      }
    } else if (step === 5) {
      try {
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 5,
          answer:
            "1.      . 2.        .",
        });
        console.log(response);
        setTimer(false);
        setFeedback5(response.data.feedback);
      } catch (err) {
        console.log(err);
      }
    } else if (step === 6) {
      try {
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 6,
          answer: answersSix,
        });
        console.log(response);
        setTimer(false);
        setFeedback6(response.data.feedback);
        onResetAnswer6();
      } catch (err) {
        console.log(err);
      }
    } else if (step === 7) {
      try {
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 7,
          answer: answerInput7,
        });
        console.log(response);
        setTimer(false);
        setFeedback7(response.data.feedback);
      } catch (err) {
        console.log(err);
      }
    } else if (step === 8) {
      try {
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 8,
          answer: answersEight,
        });
        console.log(response);
        setTimer(false);
        setFeedback8(response.data.feedback);
        onResetAnswer8();
      } catch (err) {
        console.log(err);
      }
    } else if (step === 9) {
      try {
        const response = await client.post("/judge", {
          course_id: 2,
          question_id: 9,
          answer: answerInput9,
        });
        console.log(response);
        setTimer(false);
        setFeedback9(response.data.feedback);
      } catch (err) {
        console.log(err);
      }
    }
  };

  //  
  const handleSubmit = () => {
    requestSubmitAnswer();
  };

  return (
    <Wrapper>
      <StepTitle>
        {/* id     .     /question/:id  id    */}
        <StepTitleText>  {id}</StepTitleText>
      </StepTitle>
      <StepWrapper>
        <div
          style={{
            backgroundColor: "#E6E6E6",
            width: "100%",
            height: "36px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <span style={{ fontFamily: "NanumSquareB", fontWeight: "700" }}>
            {step}
          </span>
        </div>
        <StepBox>
          <StepText />
          {/**   */}
          {step === 4 && (
            <div style={{ display: "flex", flexDirection: "column" }}>
              <WrapperForm>
                <InputBox
                  value={answerInput4}
                  onChange={(e) => setAnswerInput4(e.target.value)}
                />
              </WrapperForm>
            </div>
          )}
          {step === 7 && (
            <div style={{ display: "flex", flexDirection: "column" }}>
              <WrapperForm>
                <InputBox
                  value={answerInput7}
                  onChange={(e) => setAnswerInput7(e.target.value)}
                />
              </WrapperForm>
            </div>
          )}
          {step === 9 && (
            <div style={{ display: "flex", flexDirection: "column" }}>
              <WrapperForm>
                <InputBox
                  value={answerInput9}
                  onChange={(e) => setAnswerInput9(e.target.value)}
                />
              </WrapperForm>
            </div>
          )}
        </StepBox>
      </StepWrapper>
      <div
        style={{
          marginTop: "20px",
          width: "100%",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        {/** 1  [ ]    */}
        {step !== 1 ? (
          <PrevStepBtn onClick={toPrevStep}> </PrevStepBtn>
        ) : (
          <PrevStepBtn style={{ visibility: "hidden" }} />
        )}

        {/*    */}
        <AlertDialog.Root>
          {/* ()     */}
          <AlertDialog.Trigger>
            <SubmitBtn onClick={handleSubmit}></SubmitBtn>
          </AlertDialog.Trigger>

          {/* ()   */}
          <AlertDialog.Content maxWidth="640px">
            <AlertDialog.Title>LLM </AlertDialog.Title>
            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 1 &&
                (timer ? (
                  <Spinner style={{ height: "36px" }} />
                ) : (
                  <span>{feedback1}</span>
                ))}{" "}
            </AlertDialog.Description>

            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 2 &&
                (timer ? (
                  <Spinner style={{ height: "36px" }} />
                ) : (
                  <span>{feedback2}</span>
                ))}
            </AlertDialog.Description>
            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 3 &&
                (timer ? (
                  <Spinner
                    style={{
                      height: "36px",
                      marginTop: "10px",
                      marginLeft: "12px",
                    }}
                  />
                ) : (
                  <span>{feedback3}</span>
                ))}
            </AlertDialog.Description>
            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 4 &&
                (timer ? (
                  <Spinner
                    style={{
                      height: "36px",
                      marginTop: "10px",
                      marginLeft: "12px",
                    }}
                  />
                ) : (
                  <span>{feedback4}</span>
                ))}
            </AlertDialog.Description>
            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 5 &&
                (timer ? (
                  <Spinner
                    style={{
                      height: "36px",
                      marginTop: "10px",
                      marginLeft: "10px",
                    }}
                  />
                ) : (
                  <span>{feedback5}</span>
                ))}
            </AlertDialog.Description>
            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 6 &&
                (timer ? (
                  <Spinner
                    style={{
                      height: "36px",
                      marginTop: "10px",
                      marginLeft: "10px",
                    }}
                  />
                ) : (
                  <span>{feedback6}</span>
                ))}
            </AlertDialog.Description>
            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 7 &&
                (timer ? (
                  <Spinner
                    style={{
                      height: "36px",
                      marginTop: "10px",
                      marginLeft: "10px",
                    }}
                  />
                ) : (
                  <span>{feedback7}</span>
                ))}{" "}
            </AlertDialog.Description>
            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 8 &&
                (timer ? (
                  <Spinner
                    style={{
                      height: "36px",
                      marginTop: "10px",
                      marginLeft: "10px",
                    }}
                  />
                ) : (
                  <span>{feedback8}</span>
                ))}
            </AlertDialog.Description>
            <AlertDialog.Description
              size="4"
              style={{
                fontFamily: "NanumSquareR",
                fontWeight: "400",
                marginTop: "20px",
              }}
            >
              {step === 9 &&
                (timer ? (
                  <Spinner
                    style={{
                      height: "36px",
                      marginTop: "10px",
                      marginLeft: "10px",
                    }}
                  />
                ) : (
                  <span>{feedback9}</span>
                ))}
            </AlertDialog.Description>

            {/* ()   (  ,  ) */}
            <Flex
              gap="3"
              mt="4"
              justify="between"
              style={{ marginTop: "48px" }}
            >
              <AlertDialog.Cancel>
                <Button variant="surface" color="gray">
                  
                </Button>
              </AlertDialog.Cancel>
              <AlertDialog.Action>
                {id === "1" && step !== 9 ? (
                  <NextStepBtn onClick={toNextStep}> </NextStepBtn>
                ) : (
                  <Link to="/conclusion1">
                    <NextStepBtn>  </NextStepBtn>
                  </Link>
                )}
              </AlertDialog.Action>
            </Flex>
          </AlertDialog.Content>
        </AlertDialog.Root>
      </div>
      {step === 9 && (
        <Link to="/">
          <RestartBtn onClick={toMainPage}> </RestartBtn>
        </Link>
      )}
      <Tip />
    </Wrapper>
  );
};

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 400px;
  margin-bottom: 160px;
`;

const StepTitle = styled.div`
  width: 100%;
  height: 48px;
  background-color: #ffdf7b;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const StepTitleText = styled.span`
  font-family: NanumSquareEB;
  font-weight: 800;
  font-size: 18px;
`;

const StepWrapper = styled.div`
  width: 100%;
  margin-top: 20px;
`;
const StepBox = styled.div`
  border: 3px solid #e6e6e6;
  width: 100%;
  padding: 20px 32px;
`;

const PrevStepBtn = styled.button`
  background-color: #f4e2c1;
  border: none;
  border-radius: 30px;
  padding: 6px 24px;
  font-family: NanumSquareB;
  font-weight: 700;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const NextStepBtn = styled(PrevStepBtn)`
  background-color: #ffa57d;
`;

const SubmitBtn = styled(PrevStepBtn)`
  background-color: #ffa57d;
`;

const RestartBtn = styled.button`
  border: none;
  background-color: #f76f61;
  font-family: NanumSquareB;
  font-weight: 700;
  font-size: 20px;
  padding: 10px 24px;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 20px;
  width: 100%;
`;

/// cnrk

const WrapperForm = styled.form`
  display: flex;
  flex-direction: row;
  gap: 24px;
  margin-top: 24px;
`;
const InputBox = styled.textarea`
  border: 1px solid black;
  width: 360px;
  padding: 12px;
`;

export default QuestionBox;
