import React from "react";
import styled from "styled-components";
import icon_logo from "../img/icon_logo.svg";
import { useResetRecoilState } from "recoil";
import { stepState } from "../atoms/step";
import { client } from "../api";
import { Link } from "react-router-dom";

const Header = () => {
  //     step 
  const onResetStep = useResetRecoilState(stepState);

  const handleExitPage = async () => {
    try {
      const response = await client.delete("/session"); //  
      console.log(response);
      navigator("/");
    } catch (err) {
      console.log(err);
    }
    onResetStep();
  };

  return (
    <Wrapper>
      <Link to="/">
        <IconLogo
          src={icon_logo}
          alt="icon-logo"
          style={{ cursor: "pointer" }}
          onClick={handleExitPage}
        />
      </Link>
      <Title>    </Title>
      <BtnWrapper></BtnWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  height: 64px;
  background-color: #f76f61;
  display: flex;
  align-items: center;
  padding: 0 24px;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 2;
`;

const IconLogo = styled.img`
  height: 42px;
`;

const Title = styled.span`
  color: white;
  font-family: NanumSquareEB;
  font-weight: 800;
  font-size: 24px;
`;

const BtnWrapper = styled.div`
  display: flex;
  flex-direction: row;
`;

export default Header;
