import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { answers8 } from "../../../../atoms/answers8";
import { useSetRecoilState } from "recoil";

const StepText8 = () => {
  //      
  const [answer1, setAnswer1] = useState("");
  const [answer2, setAnswer2] = useState("");
  const [answer3, setAnswer3] = useState("");

  const setAnswers = useSetRecoilState(answers8);

  useEffect(() => {
    setAnswers(`${answer1}, ${answer2}, ${answer3}`);
  }, [answer1, answer2, answer3, setAnswers]);

  return (
    <>
      <Text style={{ flexDirection: "column" }}>
        <span> ABC  </span>
        <span> O   ,   O</span>
        <span style={{ marginBottom: "16px" }}>
           ABC{" "}
          <input
            value={answer1}
            onChange={(e) => setAnswer1(e.target.value)}
            style={{
              width: "64px",
              height: "24px",
              marginLeft: "2px",
              marginRight: "2px",
            }}
          />
           .
        </span>
        <span>
            O  ABC{" "}
          <input
            value={answer2}
            onChange={(e) => setAnswer2(e.target.value)}
            style={{
              width: "64px",
              height: "24px",
              marginLeft: "2px",
              marginRight: "2px",
            }}
          />
        </span>
        <span> ,   O</span>
        <span>
           ABC{" "}
          <input
            value={answer3}
            onChange={(e) => setAnswer3(e.target.value)}
            style={{
              width: "64px",
              height: "24px",
              marginLeft: "2px",
              marginRight: "2px",
            }}
          />
           .{" "}
        </span>
      </Text>
    </>
  );
};

const Text = styled.div`
  font-family: NanumSquareR;
  font-weight: 400;
  font-size: 18px;
  display: flex;
  flex-wrap: wrap;
  line-height: 1.6;
`;

export default StepText8;
