import React from "react";
import styled from "styled-components";
import QuestionBox from "./QuestionBox";
import { useParams } from "react-router-dom";
import { useRecoilValue } from "recoil";
import { stepState } from "../../../atoms/step";
import img1_1 from "../../../img/3_1.gif";
import img1_2 from "../../../img/3_2.gif";
import img1_3 from "../../../img/3_3.gif";
import img1_4 from "../../../img/3_4.gif";
import img1_5 from "../../../img/3_5.gif";
import img1_6 from "../../../img/3_6.gif";
import img1_7 from "../../../img/3_7.gif";
import img1_8 from "../../../img/3_8.png";

const Question = () => {
  const { id } = useParams();
  const step = useRecoilValue(stepState);

  return (
    <Wrapper>
      {id === "1" && step === 1 && <Image src={img1_1} />}
      {id === "1" && step === 2 && <Image src={img1_2} />}
      {id === "1" && step === 3 && <Image src={img1_3} />}
      {id === "1" && step === 4 && <Image src={img1_4} />}
      {id === "1" && step === 5 && <Image src={img1_5} />}
      {id === "1" && step === 6 && <Image src={img1_6} />}
      {id === "1" && step === 7 && <Image src={img1_7} />}
      {id === "1" && (step === 8 || step === 9) && <Image src={img1_8} />}

      <QuestionBox />
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  padding: 0 64px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
`;

const Image = styled.img`
  width: 800px;
  height: 420px;
  margin-left: 12px;
`;

export default Question;
