import React from "react";
import styled from "styled-components";
import icon_help from "../../../img/icon_help.svg";

const About = () => {
  return (
    <>
      <Container>
        <TopWrapper>
          <Title>#1.   </Title>
          <HelpIcon src={icon_help} alt="icon-help" />{" "}
          <StudentInfo>
            <span style={{ fontSize: "14px" }}> 2 1</span>
            <span style={{ fontSize: "16px" }}> </span>
          </StudentInfo>
        </TopWrapper>

        <DevideLine></DevideLine>
        <SubTitle>
                ,   
          .
        </SubTitle>
      </Container>
    </>
  );
};

const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  text-align: center;
  padding: 32px;
`;

const TopWrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: end;
  position: relative;
`;
const Title = styled.div`
  padding-left: 24px;
  font-family: NanumSquareB;
  font-size: 28px;
  font-weight: 700;
`;

const HelpIcon = styled.img`
  margin-left: 24px;
  margin-bottom: 4px;
  height: 20px;
`;

const StudentInfo = styled.div`
  display: flex;
  flex-direction: column;
  align-items: end;
  position: absolute;
  right: 0;
  font-family: NanumSquareR;
  font-weight: 400;
`;

const DevideLine = styled.div`
  width: 100%;
  height: 1px;
  background-color: black;
  margin: 12px 0;
`;

const SubTitle = styled.span`
  font-family: NanumSquareL;
  color: #4d4d4d;
  padding-left: 52px;
  font-weight: 300;
`;
export default About;
